import React, { Component } from 'react';
import classnames from 'classnames';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { registerUser } from '../../actions/authActions';
import PropTypes from 'prop-types';

class Register extends Component {
  constructor() {
    super();
    this.state = {
      name: '',
      email: '',
      password: '',
      password2: '',
      errors: {msg: ''}
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    if(this.props.auth.isAuthenticated) {
      this.props.history.push('/dashboard');
    }
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.errors){
      this.setState({errors: nextProps.errors});
    }
  }

  onChange(e) {
    this.setState({[e.target.name]: e.target.value})    
  }

  onSubmit(e) {
    e.preventDefault();

    const newUser = {
      name: this.state.name,
      email: this.state.email,
      password: this.state.password,
      password2: this.state.password2
    }
    this.props.registerUser(newUser, this.props.history);
  }



  render() {
    const { msg } = this.state.errors;

    const { user } = this.props.auth;
    
    return (
      <div className="register">
      {user ? user.name : null}
      <div className="container">
        <div className="row">
          <div className="col-md-8 m-auto">
            <h1 className="display-4 text-center">Sign Up</h1>
            <p className="lead text-center">Create your Stream account</p>
            <form noValidate onSubmit={this.onSubmit}>
              <div className="form-group">
                <input type="text" 
                className={classnames('form-control form-control-lg', {
                  'is-invalid': msg.name
                })}
                placeholder="Name" name="name" value={this.state.name} onChange={this.onChange}  />
                {msg.name && (<div className="invalid-feedback">{msg.name}</div>)}
              </div>
              <div className="form-group">
                <input type="email" 
                className={classnames('form-control form-control-lg', {
                  'is-invalid': msg.email
                })}
                placeholder="Email Address" name="email" value={this.state.email} onChange={this.onChange} />                
                {msg.email && (<div className="invalid-feedback">{msg.email}</div>)}
              </div>
              <div className="form-group">
                <input type="password" 
                className={classnames('form-control form-control-lg', {
                  'is-invalid': msg.password
                })}
                placeholder="Password" name="password" value={this.state.password} onChange={this.onChange} />
                {msg.password && (<div className="invalid-feedback">{msg.password}</div>)}
              </div>
              <div className="form-group">
                <input type="password" 
                className={classnames('form-control form-control-lg', {
                  'is-invalid': msg.password2
                })}
                placeholder="Confirm Password" name="password2" value={this.state.password2} onChange={this.onChange} />
                {msg.password2 && (<div className="invalid-feedback">{msg.password2}</div>)}
              </div>
              <input type="submit" className="btn btn-info btn-block mt-4" />
            </form>
          </div>
        </div>
      </div>
    </div>
    )
  }
}

Register.propTypes = {
  registerUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
  auth:state.auth,
  errors: state.errors
})

export default connect(mapStateToProps, { registerUser })(withRouter(Register));
